.wp-block-create-block-slider-working-test {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}
.slider {
    position: relative;
    max-width: 100%;
    margin: auto;
    overflow: hidden;
}

.slides {
    display: flex;
    transition: transform 0.5s ease-in-out;
}

.slide {
    min-width: 100%;
    box-sizing: border-box;
    position: relative;
}

.slide img {
    width: 100%;
    vertical-align: middle;
}

.caption {
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    width: 100%;
    height: 100%;
    display: flex;
	flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: rgba(0, 0, 0, 0.5);

}

.navigation {
    position: absolute;
    top: 50%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    transform: translateY(-50%);
}

.navigation button {
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.navigation button:hover {
    background-color: rgba(0, 0, 0, 0.8);
}

/* Responsive adjustments */
@media (max-width: 600px) {
    .caption {
        font-size: 14px;
        bottom: 10px;
    }

    .navigation button {
        padding: 5px;
    }
}